	var wxCharts = require("../../../utils/wxcharts.js");
	var _self;
	var Charts1;
	var Charts2;
	
	var Data = {
		categories: ['1号教学楼', '2号教学楼', '行政楼'],
		series: [{
				name: 'AQI',
				data: [50, 46, 41]
			}
		]
	};
	var Data3 = {
		categories: ['同比'],
		series: [{
				name: '同比',
				data: [90]
			}
		]
	};
	var Data1 = {
		categories: ['环比'],
		series: [{
				name: '环比',
				data: [93]
			}
		]
	};
	var Data2 = {
		series: [{
				data: 85,
				name: '联网'
			},
			{
				data: 15,
				name: '断网'
			}
		]
	};
	var width;
	export default {
		onLoad: function(event) {
			_self = this;
			// 			uni.setNavigationBarTitle({
			// 				title: '新的标题'
			// 			});
			// uni.showLoading({
			// 	title: '加载中...',
			// 	mask:true,
			// });
			var detail = new Object();
			detail = JSON.parse(decodeURIComponent(event.detail))
			
			try {
				this.onload = JSON.parse(decodeURIComponent(event.detail));
			} catch (error) {
				this.onload = JSON.parse(event.detail);
			}
			console.log(this.onload);
			uni.setNavigationBarTitle({
				title: this.onload.storeName+'室外空气'
			});	
			
			uni.getSystemInfo({
				success(res) {
					width = res.screenWidth - 10;
					_self.pixelRatio =2;
				}
			})
			this.cWidth3=uni.upx2px(135);
			this.cHeight3=uni.upx2px(135);
		},
		data() {
			return {
				    cWidth3:'',//圆弧进度图
				    cHeight3:'',//圆弧进度图
                    pixelRatio:1
			}
		},
		onReady: function() {
			this.ShowCharts("charts", Data);
			this.showArcbar3("canvasArcbar3", Data3);
			this.showArcbar3("canvasArcbar1", Data1);
			this.ShowCharts2("canvasArcbar2", Data2);
			//this.hideLoading();
		},
		methods: {
			ShowCharts2: function(canvasId, data) {
				Charts2 = new wxCharts({
					canvasId: canvasId,
					type: 'pie',
					fontSize: 11,
					background: '#FFFFFF',
					animation: true,
					series: data.series,
					width: 165,
					height: 165,
					dataLabel: true,
					pixelRatio:1,
				});
			},
			touch:function(e){
				console.log(e)
				// Charts1.getCurrentDataIndex('renderComplete', (e) => {
				// 	// your code here
				// 	console.log(e)
				// });
				// Charts1.getCurrentDataIndex(e, {
				// 	// format: function (item, category) {
				// 	// 	return category + ' ' + item.name + ':' + item.data 
				// 	// }
				// });
			},
			/*显示图表*/
			ShowCharts: function(canvasId, data) {
				//console.log(data)
				Charts1 = new wxCharts({
					canvasId: canvasId,
					type: 'column',
					legend: true,
					fontSize: 10,
					background: '#FFFFFF',
					animation: true,
					categories: data.categories,
					series: data.series,
					width: 350,
					height: 350,
					pixelRatio:1,
					rotate:true,  //横屏模式
					xAxis: {
						disableGrid:false	,
					},
					yAxis: {
						disabled:false
					},
					dataLabel: true,
					extra: {
						column: {
						  width: 300//_self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
						}
					  }
				});
			},
			
			/*显示图表*/
			showArcbar3(canvasId,chartData){
				console.log(chartData);
				
				new wxCharts({
					canvasId: canvasId,
					type: 'arcbar',
					fontSize:11,
					legend:false,
					title: {
						name: chartData.series[0].data+'%',
						color: '#667766',
						fontSize: 10
					},
					subtitle: {
						name: chartData.series[0].name,
						color: '#666666',
						fontSize: 10
					},
					extra: {
						arcbar:{
							type:'default',//整圆类型进度条图
							width: 10,//_self.arcbarWidth*_self.pixelRatio,//圆弧的宽度
							startAngle:0,//整圆类型只需配置起始角度即可
							backgroundColor:'#ffe3e8',
						},
						ringChart:1000
					},
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					series: chartData.series,
					animation: true,
					width: _self.cWidth3*_self.pixelRatio,
					height: _self.cHeight3*_self.pixelRatio,
					dataLabel: true,
				
				});
				
			}
		}
	}
